import ListGroup from 'react-bootstrap/ListGroup'
import Figure from 'react-bootstrap/Figure'
import Table from 'react-bootstrap/Table'
let Question = (props) => {

    let choiceSelected = (choice) => {
        let isCorrect = props.question.answer === choice
        props.choiceSelected(isCorrect)
    }

    return (
        <Table>
            <tbody>
                <tr>
                    <td>
                        <Figure>
                            <Figure.Image

                                alt={'flower image'}
                                src={props.question.picture}
                            />
                            <Figure.Caption>

                            </Figure.Caption>

                        </Figure>
                    </td>
                    <td>
                        <ListGroup>
                            {props.question.choices.map(choice => <ListGroup.Item onClick={() => choiceSelected(choice)} action variant="light">
                                {choice}
                            </ListGroup.Item>)}

                        </ListGroup>
                    </td>
                </tr>
            </tbody>
        </Table>
    )
}

export default Question